.Header {
	-webkit-user-select: none;
	-moz-user-select: none;
	 -ms-user-select: none;
	     user-select: none;
}

.Div {
	-webkit-user-select: none;
	-moz-user-select: none;
	 -ms-user-select: none;
	     user-select: none;
}

.show {
	-webkit-animation: 1s ease-in-out 1 forwards move;
	        animation: 1s ease-in-out 1 forwards move;
	bottom: calc(var(--tabbar_height) + 5px);
}

.hide {
	animation: 0.5s ease-in-out 1 reverse forwards move;
}

@-webkit-keyframes move {
	from {
		bottom: 0;
	}
	,
	50% {
		bottom: calc(var(--tabbar_height) / 2);
	}
	to {
		bottom: calc(var(--tabbar_height) + 5px);
	}
}

@keyframes move {
	from {
		bottom: 0;
	}
	,
	50% {
		bottom: calc(var(--tabbar_height) / 2);
	}
	to {
		bottom: calc(var(--tabbar_height) + 5px);
	}
}

.PopupView {
	border-radius: 10px;
	background-color: var(--background_light);
	position: fixed;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	margin-top: 10px;
	height: calc(100% - 56px - 24px - var(--tabbar_height));
	width: calc(100% - 40px);
}

.PopupBlock {
	background: rgba(0, 0, 0, 0.5);
	display: -webkit-flex;
	display: flex;
	height: 100%;
	width: 100%;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
}

.PopupHeader {
	color: var(--header_text);
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	display: -webkit-flex;
	display: flex;
	border-top-left-radius: 10px;
	border-top-right-radius: 10px;
	height: 44px;
	width: 100%;
	background: var(--header_background);
}

.PopupBottom {
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	display: -webkit-flex;
	display: flex;
	bottom: 0;
	min-height: 44px;
	width: calc(100% - 10px);
	padding: 5px;
}

.PopupHeaderClose {
	margin: 12px;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: flex-end;
	        justify-content: flex-end;
}

.PopupHeaderText {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-grow: 1;
	        flex-grow: 1;
	-webkit-justify-content: center;
	        justify-content: center;
}

.PopupContent {
	padding: 5px;
	text-indent: 5mm;
	overflow: scroll;
	color: var(--text_primary);
}

.Show {
	-webkit-animation: 1s ease-in-out 1 forwards move;
	        animation: 1s ease-in-out 1 forwards move;
}

.Hide {
	animation: 1s ease-in-out 1 reverse forwards move;
}

@-webkit-keyframes move {
	0% {
		opacity: 0;
	}
	40% {
		opacity: 0.5;
	}
	100% {
		opacity: 1;
	}
}

@keyframes move {
	0% {
		opacity: 0;
	}
	40% {
		opacity: 0.5;
	}
	100% {
		opacity: 1;
	}
}

